import React, { Component } from "react";
import { Route, Switch } from "react-router-dom";
import NotificationSystem from "react-notification-system";

import AdminNavbar from "components/Navbars/AdminNavbar";
import Footer from "components/Footer/Footer";
import Sidebar from "components/Sidebar/Sidebar";
import ModalCrearConsulta from "components/Modal/ModalCrearConsulta";
// import FixedPlugin from "components/FixedPlugin/FixedPlugin.jsx";

import { style } from "variables/Variables.jsx";

import routes from "routes.js";

import image from "assets/img/sidebar-3.jpg";

//Firebase
import * as firebase from "firebase/app";
import { firebaseConfig } from 'variables/Config'
import 'firebase/database';
import { isConstructorDeclaration } from "typescript";

class Admin extends Component {
  constructor(props) {
    super(props);
    this.state = {
      _notificationSystem: null,
      image: image,
      color: "black",
      hasImage: true,
      fixedClasses: "dropdown show-dropdown open",
      consultas: [],
      resultados: [],
      consultaActual: null,
      shown: [false],
      tags: [],
      nameConsulta: '',
      geocode: '4.115350699646785,-72.584710852785,1500km',
      language: 'es'
    };

    if (firebase.apps.length === 0) {
      this.app = firebase.initializeApp(firebaseConfig, 'app_jaja');
      this.db_resultados = this.app.database().ref('resultados')
      this.db_consultas = this.app.database().ref('consultas')
    }
  }

  handleChangeLanguage = (e) => {
    let {language} = this.state;
    language = e;
    this.setState({language});
  }

  handleChangeMapData = (e) => {
    let {geocode} = this.state;
    geocode = e;
    this.setState({geocode});
  }

  setConsultaActual = (id) => {
    console.log("idConsulta", id)
    let { consultas, consultaActual, resultados } = this.state;
    let consulta = consultas.find(d => d.id == id)
    if (consulta){
      consultaActual = consulta;
      consultaActual.resultado = consultaActual.resultado || resultados.find(d => d.id == consultaActual.id);
    }

    this.setState({ consultaActual })
  }

  crearConsulta = () => {
    let { tags, nameConsulta, geocode, language } = this.state;
    if (tags.length >= 0) {
      fetch('https://us-central1-smdt-ae3d7.cloudfunctions.net/addQuery', {
        method: 'POST',
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          query: tags.join(";"),
          name: nameConsulta,
          radius: geocode,
          lang: language,
          status: 'waiting'
        })
      }).then(res => {
        if (res.status === 200) {
          this.setState({ tags: [] })
          this.state._notificationSystem.addNotification({
            title: <span data-notify="icon" className="pe-7s-check" />,
            message: (
              <div>
                Consulta creada <b>correctamente</b>.
              </div>
            ),
            level: "success",
            position: "tr",
            autoDismiss: 5
          });
        }
      })
    }
  }

  handleItem = (num) => {
    const { shown } = this.state;
    shown[num] = !shown[num];
    this.setState({ shown })
  }

  handleNotificationClick = position => {
    var color = Math.floor(Math.random() * 4 + 1);
    var level;
    switch (color) {
      case 1:
        level = "success";
        break;
      case 2:
        level = "warning";
        break;
      case 3:
        level = "error";
        break;
      case 4:
        level = "info";
        break;
      default:
        break;
    }
    this.state._notificationSystem.addNotification({
      title: <span data-notify="icon" className="pe-7s-gift" />,
      message: (
        <div>
          Welcome to <b>Light Bootstrap Dashboard</b> - a beautiful freebie for
          every web developer.
        </div>
      ),
      level: level,
      position: position,
      autoDismiss: 15
    });
  };
  getRoutes = routes => {
    return routes.map((prop, key) => {
      if (prop.layout === "/smdt") {
        return (
          <Route
            path={prop.layout + prop.path}
            render={props => (
              <prop.component
                {...props}
                handleClick={this.handleNotificationClick}
                consulta={this.state.consultaActual}
                resultado={this.state.resultado}
              />
            )}
            key={key}
          />
        );
      } else {
        return null;
      }
    });
  };
  getBrandText = path => {
    for (let i = 0; i < routes.length; i++) {
      if (
        this.props.location.pathname.indexOf(
          routes[i].layout + routes[i].path
        ) !== -1
      ) {
        return routes[i].name;
      }
    }
    return "Brand";
  };
  handleImageClick = image => {
    this.setState({ image: image });
  };
  handleColorClick = color => {
    this.setState({ color: color });
  };
  handleHasImage = hasImage => {
    this.setState({ hasImage: hasImage });
  };
  handleFixedClick = () => {
    if (this.state.fixedClasses === "dropdown") {
      this.setState({ fixedClasses: "dropdown show-dropdown open" });
    } else {
      this.setState({ fixedClasses: "dropdown" });
    }
  };

  addTag = (tag, nameConsulta) => {
    let { tags } = this.state;
    tags.push(tag);
    this.setState({ tags })
    this.setState({ nameConsulta: nameConsulta })
  }

  removeTag = (tag) => {
    let { tags } = this.state;
    tags.pop(tag);
    this.setState({ tags })
  }

  componentDidMount() {
    this.setState({ _notificationSystem: this.refs.notificationSystem });
    var _notificationSystem = this.refs.notificationSystem;
    var color = Math.floor(Math.random() * 4 + 1);
    var level;
    switch (color) {
      case 1:
        level = "success";
        break;
      case 2:
        level = "warning";
        break;
      case 3:
        level = "error";
        break;
      case 4:
        level = "info";
        break;
      default:
        break;
    }
    let { consultas, consultaActual, resultados } = this.state;
    this.db_consultas
      .on('child_added', snap => {
        // let query = snap.val().query ? snap.val().query.split(";") : null;
        consultas.push({
          id: snap.key,
          name: snap.val().nameQuery,
          query: snap.val().query,
          status: snap.val().status
        });
        consultaActual = consultaActual || consultas[consultas.length - 1]
        consultaActual.resultado = consultaActual.resultado || resultados.find(d => d.id == consultaActual.id)
        this.setState({ consultas, consultaActual });
      });

      this.db_consultas
      .on('child_changed', snap => {
        console.log("cambio", snap.key);
        // let query = snap.val().query ? snap.val().query.split(";") : null;
        let newOb = {
          id: snap.key,
          name: snap.val().nameQuery,
          query: snap.val().query,
          status: snap.val().status

        }

        consultas.forEach((element, index) => {
          if(element.id === snap.key){
            consultas[index] = newOb
          }
        });
      
        this.setState({ consultas });
      });

    this.db_resultados
      .on('child_added', snap => {
        resultados.push({
          id: snap.key,
          date: snap.val().date,
          keywords_analysis: snap.val().keywords_analysis,
          retweets_analysis: snap.val().retweets_analysis,
          word_cloud: snap.val().word_cloud || [{text: 'sin palabras', value: 100}]
        });
        consultaActual.resultado = consultaActual.resultado || resultados.find(d => d.id == consultaActual.id)
        this.setState({ resultados, consultaActual });
      });

  }
  componentDidUpdate(e) {
    if (
      window.innerWidth < 993 &&
      e.history.location.pathname !== e.location.pathname &&
      document.documentElement.className.indexOf("nav-open") !== -1
    ) {
      document.documentElement.classList.toggle("nav-open");
    }
    if (e.history.action === "PUSH") {
      document.documentElement.scrollTop = 0;
      document.scrollingElement.scrollTop = 0;
      this.refs.mainPanel.scrollTop = 0;
    }
  }
  render() {
    return (
      <div className="wrapper">
        <NotificationSystem ref="notificationSystem" style={style} />
        <Sidebar {...this.props} routes={routes} image={this.state.image}
          color={this.state.color}
          consultas={this.state.consultas}
          hasImage={this.state.hasImage}
          handleItem={this.handleItem} />
        <div id="main-panel" className="main-panel" ref="mainPanel">
          <AdminNavbar
            {...this.props}
            setConsultaActual={this.setConsultaActual}
            brandText={this.getBrandText(this.props.location.pathname)}
            consultas={this.state.consultas}
            handleItem={this.handleItem}
          />
          <Switch>{this.getRoutes(routes)}</Switch>
          {/* <Footer /> */}
          <ModalCrearConsulta handleChangeMapData={this.handleChangeMapData} handleChangeLanguage={this.handleChangeLanguage} crearConsulta={this.crearConsulta} addTag={this.addTag} removeTag={this.removeTag} tags={this.state.tags} handleItem={this.handleItem} shown={this.state.shown[0]} />
        </div>
      </div>
    );
  }
}

export default Admin;
