import React, { Component } from 'react'
import { Form, FormGroup, ControlLabel, FormControl, Grid, Row, Col } from 'react-bootstrap';
export default class FormTags extends Component {


    state = {
        prox: '',
        name: ''
    };

    handleChange = e => {
        this.setState({ prox: e.target.value });
    }

    handleChangeName = e => {
        this.setState({ name: e.target.value });
    }

    handleSubmit = e => {
        e.preventDefault();
        e.stopPropagation();
        this.props.addTag(this.state.prox, this.state.name)
        this.setState({ prox: '' })
    }

    handleSubmitName = e => {
        e.preventDefault();
        e.stopPropagation();
    }

    render() {
        return (
            <Grid fluid>
                <Row>
                    <Col lg={6} sm={6} xs={12}>
                        <Form onSubmit={this.handleSubmitName}>
                            <FormGroup style={{width: '100%'}}>
                                <ControlLabel> Nombre de la consulta </ControlLabel>
                                <FormControl
                                    type="text"
                                    value={this.state.name}
                                    placeholder="Ingrese nombre"
                                    onChange={this.handleChangeName}
                                />
                                <FormControl.Feedback />
                            </FormGroup>
                        </Form>
                    </Col>
                    <Col lg={6} sm={6} xs={12}>
                        <Form onSubmit={this.handleSubmit}>
                            <FormGroup style={{width: '100%'}}>
                                <ControlLabel> Palabra clave a consultar </ControlLabel>
                                <FormControl
                                    type="text"
                                    value={this.state.prox}
                                    placeholder="Ingrese concepto"
                                    onChange={this.handleChange}
                                />
                                <FormControl.Feedback />
                            </FormGroup>
                        </Form>
                    </Col>
                </Row>
            </Grid>
        )
    }
}
