import React, { Component } from 'react'
import './Ranger.css'
export default class Ranger extends Component {


    onChange = ({ target: { value: value } }) => {
        this.props.handleChangeRad(value)
    }

    render() {
        return (
            <div>
                <form className="form_range">
                    <input type="range" step={10} min={1} max={7000} value={(this.props.rad)} onChange={this.onChange} className={'form-control'} />
                    <label className='labelRange'>{(this.props.rad / 2).toFixed(0) + 'km'}</label>
                </form>
            </div>
        )
    }
}
