import React, { Component } from 'react'
import Tag from './Tag'

export default class ListaTags extends Component {

    render() {
        return (
            <div>
                {this.props.tags.map((d, index) => {
                    return <Tag tag={d} key={index} removeTag={this.props.removeTag} tagEditable={true}/>
                })}
            </div>
        )
    }
}
