import React, { Component } from 'react'
import { Table } from 'react-bootstrap';

export default class LanguageSelector extends Component {

    state = {
        languages: [
            {
                code: 'es',
                icon: '🇪🇸'
            },{
                code: 'en',
                icon: '🇺🇸'
            },{
                code: 'por',
                icon: '🇧🇷'
            },{
                code: 'fr',
                icon: '🇫🇷'
            }
        ]
    }

    flagButtonStyle = {
        textAlign: 'center'
    }

    flagIconStyle = {
        fontSize: '28px',
        cursor: 'pointer'
    }

    handleLanguageClick = (e) => {
        this.props.handleChangeLanguage(e)
    }
    render() {
        return (
            <div>
                <Table striped>
                    <thead>
                        <tr>
                            {this.state.languages.map(language => {
                                return <th key={language.code} style={this.flagButtonStyle}><span style={this.flagIconStyle} onClick={() => this.handleLanguageClick(language.code)} role="img" aria-label="sheep">{language.icon}</span></th>
                            })}
                        </tr>
                    </thead>
                </Table>
            </div>
        )
    }
}
