import React, { Component } from 'react'
import { Button, Modal } from "react-bootstrap";
import FormTags from 'components/FormTags/FormTags'
import ListaTags from 'components/ListaTags/ListaTags'
import Map from 'components/Map/Map'
import LanguageSelector from 'components/LanguageSelector/LanguageSelector';
export default class ModalCrearConsulta extends Component {



    createLegend = (json) => {
        var legend = [];
        for (var i = 0; i < json["names"].length; i++) {
            var type = "fa fa-circle text-" + json["types"][i];
            legend.push(<i className={type} key={i} />);
            legend.push(" ");
            legend.push(json["names"][i]);
        }
        return legend;
    }

    handleItemClick = (e) => {
        this.props.crearConsulta()
        this.props.handleItem(0)
    }

    handleClose = (e) => {
        this.props.handleItem(0)
    }

    render() {
        return (
            <Modal show={this.props.shown} onHide={this.handleClose}>
                <Modal.Header closeButton>
                    <Modal.Title>Crear consulta</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <FormTags addTag={this.props.addTag}/>
                    <ListaTags tags={this.props.tags} removeTag={this.props.removeTag} />
                    <hr style={{ marginBottom: '0' }} />
                    <LanguageSelector handleChangeLanguage={this.props.handleChangeLanguage} />
                    <Map handleChangeMapData={this.props.handleChangeMapData} />
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={this.handleClose}>
                        Cerrar
              </Button>
                    <Button variant="primary" onClick={this.handleItemClick}>
                        Guardar
              </Button>
                </Modal.Footer>
            </Modal>
        )
    }
}
